import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { observer } from '@core/functions/observer.function';
import { Address } from '@core/interfaces/address.interface';
import { User } from '@core/interfaces/user';
import { AppStoreService } from '@core/store/app-store.service';
import { AppState } from '@core/store/app-store.state';
import { SessionService } from '@services/session/session.service';
import { SwaService } from '@services/swa/swa.service';
import { Observable, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-account-page',
  templateUrl: './account-page.component.html',
  styleUrls: ['./account-page.component.scss'],
})
export class AccountPageComponent implements OnInit {
  state$: Observable<AppState> = new Subject<AppState>();
  protected ngUnsubscribe: Subject<void> = new Subject<void>();

  constructor(
    public store: AppStoreService,
    public swa: SwaService,
    private router: Router,
    private session: SessionService
  ) {
    this.state$ = this.store.getState();
  }

  ngOnInit(): void {
    this.swa.loading();
    this.store
      .loadHome()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
        observer(this.swa, () => {
          this.loadShoppingCart();
        })
      );
  }

  loadShoppingCart() {
    this.store;
    this.store
      .loadShoppingCart()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(observer(this.swa));
  }

  onCloseSession(data: any) {
    this.session.logout();
    this.router.navigate(['/auth/login']);
  }

  showEditUserForm() {
    this.store.showEditUserForm(true);
  }

  closeEditUSerForm() {
    this.store.showEditUserForm(false);
  }

  editUser($event: User) {
    this.swa.confirm('¿ Desea guardar los cambios?', '', (result) => {
      if (result.value) {
        this.store
          .updateUser($event)
          .pipe(takeUntil(this.ngUnsubscribe))
          .subscribe(
            observer(this.swa, () => {
              this.swa.success('Cambios guardados correctamente');
              this.closeEditUSerForm();
            })
          );
      }
    });
  }

  showFormAddress(item?: Address) {
    this.store.showEditAddressForm(true, item);
  }

  showFormCreateAddress() {
    this.store.showAddAddressForm(true);
  }

  closeFormAddress() {
    this.store.showEditAddressForm(false);
  }

  closeFormAddAddress() {
    this.store.showAddAddressForm(false);
  }

  showEditDeliveryForm(item?: Address) {
    this.store.showEditDeliveryForm(true, item);
  }

  closeFormDelivery() {
    this.store.showEditDeliveryForm(false);
  }

  deleteAddress(item?: Address) {
    this.swa.confirm('¿Desea eliminar la dirección?', '', (result) => {
      if (result.value) {
        this.swa.loading;
        this.store
          .deleteAddress(item?.id)
          .pipe(takeUntil(this.ngUnsubscribe))
          .subscribe(
            observer(this.swa, () => {
              this.swa.close();
              this.swa.success('Dirección eliminada');
            })
          );
      }
    });
  }

  createAddress(event$: Address) {
    this.swa.confirm('¿Desea agregar esta dirección?', '', (result) => {
      if (result.value) {
        this.swa.loading;
        this.store
          .saveAddress(event$)
          .pipe(takeUntil(this.ngUnsubscribe))
          .subscribe(
            observer(this.swa, () => {
              this.swa.close();
              this.closeFormAddAddress();
              this.swa.success('Dirección agregada');
            })
          );
      }
    });
  }

  editAddress(event$: Address, currentAddress?: Address) {
    this.swa.confirm('¿Desea guardarlos cambios?', '', (result) => {
      if (result.value) {
        this.swa.loading;
        this.store
          .editAddress(event$, currentAddress)
          .pipe(takeUntil(this.ngUnsubscribe))
          .subscribe(
            observer(this.swa, () => {
              this.swa.close();
              this.closeFormAddress();
              this.swa.success('Dirección actualizada');
            })
          );
      }
    });
  }

  editDelivery(deliveryId: number) {
    this.swa.confirm('¿Desea guardar los cambios?', '', (result) => {
      if (result.value) {
        this.swa.loading;
        this.store
          .editDelivery(deliveryId)
          .pipe(takeUntil(this.ngUnsubscribe))
          .subscribe(
            observer(this.swa, () => {
              this.swa.close();
              this.closeFormDelivery();
              this.swa.success('Horario actualizado');
            })
          );
      }
    });
  }
}
